body {
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weeks {
  display: inline-block;
  border: 1px solid #524a54;
  font-size: 5px;
  cursor: pointer;
  animation-delay: 3s;
  -webkit-animation-delay: 3s;
  animation: fadeInAnimation 2s linear;
}

.completed {
  background: #380000;
  color: #357436;
  border: 1px solid #83558f;
}

.dateofbirth {
  height: 25px;
  width: 156px;
  border-radius: 0px;
  color: #52703b;
  margin-bottom: 5px;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}